<template>
  <div>
    <v-img
      @mousemove="$emit('isEditData', gallery.source)"
      @mouseout="$emit('isEditData', false)"
      class="gallery__list__image"
      width="500"
      height="300"
      v-if="gallery"
      :src="'/' + gallery.source"
    >
      <div class="d-flex justify-space-between">
        <transition name="magic">
          <v-icon
            class="gallery__list__icon"
            v-show="isEdit == gallery.source || isEditRed == gallery.source"
            @mousemove="$emit('isEditData', gallery.source)"
            @click="$emit('isEditRedData', gallery.source)"
          >edit</v-icon>
        </transition>
        <v-icon
          class="gallery__list__icon"
          v-show="isEdit == gallery.source"
          @mousemove="$emit('isEditData', gallery.source)"
          @click="imageRemove(gallery)"
          >cancel</v-icon
        >
      </div>
      <transition name="magic">
        <div
          @mousemove="$emit('isEditData', gallery.source)"
          v-show="isEdit == gallery.source && !isEditRed"
          class="gallery__list__info"
        >
          <div class="d-flex flex-row align-center">
            <h2>{{ gallery.name }}</h2>
            <p v-if="gallery.date" style="margin-top: 15px; margin-left: 20px;">
              Опубликовано {{ gallery.date }}
            </p>
          </div>
          <p>{{ gallery.description }}</p>
        </div>
      </transition>
      <div v-if="isEditRed == gallery.source" class="gallery__list__edit">
        <v-text-field
          v-model="galleryName"
          v-if="gallery.name"
          solo
          :label="gallery.name"
        ></v-text-field>
        <v-text-field
          v-model="galleryName"
          v-else
          solo
          label="Введите название изображения"
        ></v-text-field>

        <v-text-field
          v-model="galleryDescription"
          v-if="gallery.description"
          solo
          :label="gallery.description"
        >
        </v-text-field>
        <v-text-field
          v-model="galleryDescription"
          v-else
          solo
          label="Введите описание изображения"
        ></v-text-field>
        <div class="d-flex justify-space-around">
          <v-btn
            @click="editSave(gallery)"
            v-if="galleryDescription.length < 270"
            color="success"
          >
            <v-icon>save</v-icon>
          </v-btn>
          <v-btn @click="$emit('isMaxLengthData', true)" v-else color="error">
            <v-icon>save</v-icon>
          </v-btn>
          <v-btn @click="$emit('isEditRedData', false)" color="error">
            <v-icon>cancel</v-icon>
          </v-btn>
        </div>
      </div>
    </v-img>
  </div>
</template>

<script>
import axios from "axios"
import "../../scss/galleryList.scss";

export default {
  name: "GalleryImage",
  data() {
    return {
      galleryName: "",
      galleryDescription: "",
    }
  },
  props: ["gallery", "isEdit", "isEditRed", "isMaxLength"],
  methods: {
    async editSave(source) {
      let res;
      if (this.galleryName && this.galleryDescription) {
        res = await axios.put("/api/users/image_edit/" + this.$route.params.id, 
        { name: this.galleryName, description: this.galleryDescription, source: source.source})
      } 
      if (this.galleryName && !this.galleryDescription) {
         res = await axios.put("/api/users/image_edit/" + this.$route.params.id, 
        { name: this.galleryName, description: source.description, source: source.source})
      }
      if (!this.galleryName && this.galleryDescription) {
         res = await axios.put("/api/users/image_edit/" + this.$route.params.id, 
        { name: source.name, description: this.galleryDescription, source: source.source})
      }
      if (!this.galleryName && !this.galleryDescription) {
        res = await axios.put("/api/users/image_edit/" + this.$route.params.id, 
        { name: source.name, description: source.description, source: source.source})
      }
      localStorage.setItem("user", JSON.stringify(res.data))
      this.$emit("isEditRedData", false)
    },
    async imageRemove(image) {
      let res = await axios.delete("/api/users/image_delete/" + this.$route.params.id,
      { data: { image: image.source }})
      localStorage.setItem("user", JSON.stringify(res.data))
    }
  }
};
</script>
